@import url('https://fonts.googleapis.com/css2?family=Inter:wght@400;500;700&family=IBM+Plex+Mono:wght@400;700&display=swap');

@import url('./theme.css');

html, body {
  height: 100%;
  margin: 0;
  padding: 0;
  width: 100vw;
  min-width: 0;
  overflow-x: hidden !important;
  font-family: 'Inter', 'IBM Plex Mono', 'Lato', 'Roboto', 'Segoe UI', Arial, sans-serif;
  background: linear-gradient(120deg, #faf5ff 0%, #e0f2fe 100%) fixed;
  color: #22223B;
  min-height: 100vh;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
}

#root, .App {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  flex: 1 0 auto;
}

/* Max-width container for all content */
.container {
  max-width: 1100px;
  margin: 0 auto;
  padding: 0 1.5rem;
}

/* SVG wave background for hero/header */
.hero-bg {
  position: absolute;
  top: 0; left: 0; width: 100vw; height: 340px;
  z-index: 0;
  pointer-events: none;
}

/* Glassmorphism card/section */
.trend-card, .map-card, .card, .demographic-card, .section-context, .demographic-section {
  margin: 0; /* spacing via cards-container gap */
  overflow: visible; /* allow full visuals */
  max-width: 99vw;
  width: 99%;
  min-width: 0;
  box-sizing: border-box;
  padding: clamp(1rem, 3vw, 2.2rem);
  border-radius: 1.25rem;
  background: rgba(255,255,255,0.85);
  box-shadow: 0 4px 24px 0 rgba(124, 86, 255, 0.07);
  border: 1.5px solid #e9d5ff;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  font-size: 1.14rem;
  color: #3b0764;
  line-height: 1.72;
  transition: box-shadow 0.18s, border 0.18s, transform 0.18s;
}

/* Tighter spacing for Income and Cardiovascular Disease Analysis cards */
#income-analysis-section .trend-card,
#cardio-analysis-section .trend-card,
#income-analysis-section .card,
#cardio-analysis-section .card {
  margin-top: 0.7rem !important;
}

/* Section headers */
.section-header {
  font-family: 'IBM Plex Mono', monospace;
  font-weight: 700;
  font-size: 2.2rem;
  color: #7F4DE2;
  margin-bottom: 1.2rem;
}

/* Tag */
.tag {
  font-family: 'IBM Plex Mono', monospace;
  font-weight: 400;
  font-size: 1.1rem;
  background: #ede9fe;
  color: #a21caf;
  padding: 0.2rem 0.5rem;
  border-radius: 6px;
}

/* Card hover effect */
.trend-card:hover, .map-card:hover, .card:hover, .demographic-card:hover, .section-context:hover, .demographic-section:hover {
  box-shadow: 0 8px 32px 0 rgba(124, 86, 255, 0.13);
  border: 1.5px solid #b993ff;
  transform: translateY(-1.5px) scale(1.012);
}

.card-visual {
  width: 100%;
  max-width: 70%; /* Consistent width */
  min-width: 0;
  overflow: visible; /* Allow full visuals */
  max-height: none;
  box-sizing: border-box;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0 auto 1.2rem auto;
}
.card-visual-sm {
  max-width: 60%;
  max-height: 260px;
}
.card-visual svg, .card-visual canvas {
  width: 100% !important;
  max-width: 100% !important;
  height: auto !important;
  max-height: none !important;
}
@media (max-width: 900px) {
  .card-visual {
    max-width: 90%;
    max-height: none;
  }
  .card-visual-sm {
    max-width: 85%;
    max-height: 190px;
  }
  .card-visual svg, .card-visual canvas {
    max-height: none !important;
  }
}
@media (max-width: 600px) {
  .card-visual {
    max-width: 99vw;
    max-height: none;
  }
  .card-visual-sm {
    max-width: 99vw;
    max-height: 120px;
  }
  .card-visual svg, .card-visual canvas {
    max-height: none !important;
  }
}

.card-visual svg {
  display: block;
  width: 100%;
  max-width: 100%;
  min-width: 0;
  height: auto;
  object-fit: contain;
  margin: 0 auto;
  border-radius: 1.1rem;
  background: #fff;
  box-sizing: border-box;
}
@media (max-width: 600px) {
  .card-visual svg {
    height: 100%;
    min-height: 180px;
  }
}

/* Section containers */
.trends-container, .maps-container, .correlation-container, .demographics-container {
  margin: 0; /* spacing via cards-container */
  display: flex;
  flex-direction: column;
  gap: 2.2rem;
  align-items: center;
  justify-content: center;
  width: 100%;
  max-width: 900px;
}

.cards-container {
  max-width: 1100px;
  margin: 1.5rem auto;
  padding: 2.2rem 2.2rem 2rem 2.2rem;
  border-radius: 1.7rem;
  background: linear-gradient(120deg, #f3f0ff 0%, #e0f2fe 100%);
  box-shadow: 0 8px 32px 0 rgba(124, 86, 255, 0.10);
  display: flex;
  flex-direction: column;
  align-items: stretch;
  gap: 2.2rem;
  position: relative;
}
.container-title {
  font-family: 'IBM Plex Mono', monospace;
  text-transform: uppercase;
  font-size: 1.18rem;
  font-weight: 700;
  color: #7F4DE2;
  letter-spacing: 0.11em;
  margin-bottom: 1.3rem;
  margin-top: 0.2rem;
  text-align: left;
  padding-left: 0.1em;
}
@media (max-width: 900px) {
  .cards-container {
    max-width: 99vw;
    padding: 1.2rem 0.7rem 1.4rem 0.7rem;
    margin: 1.1rem auto 1.5rem auto;
    gap: 1.2rem;
  }
  .container-title {
    font-size: 1.08rem;
    margin-bottom: 0.9rem;
  }
}
@media (max-width: 600px) {
  .cards-container {
    padding: 1.1rem 0.3rem 1.1rem 0.3rem;
    margin: 1.1rem auto 1.3rem auto;
    gap: 0.7rem;
  }
  .container-title {
    font-size: 1rem;
    margin-bottom: 0.6rem;
  }
}

/* Responsive improvements */
@media (max-width: 900px) {
  .container {
    padding: 0 0.3rem;
  }
  .trend-card, .map-card, .card, .demographic-card, .section-context, .demographic-section {
    margin: 1.2rem auto;
    padding: 1.2rem 0.7rem;
    max-width: 99vw;
    font-size: 1.08rem;
  }
  .trends-container, .maps-container, .correlation-container, .demographics-container {
    padding: 0 0.3rem;
    gap: 1.2rem;
  }
  .App-header {
    padding: 1.2rem 0.5rem 1.2rem 0.5rem;
    margin-left: 0;
    margin-right: 0;
    max-width: 99vw;
  }
}

/* Navbar */
.glassy-navbar {
  width: 100%;
  background: linear-gradient(120deg, #fff 0%, #f3f4f6 100%);
  box-shadow: 0 4px 24px 0 rgba(124, 86, 255, 0.07);
  border: 1.5px solid #e9d5ff;
  border-radius: 1.25rem;
  padding: 1rem 2rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
  position: sticky;
  top: 0;
  z-index: 999;
}

.navbar-content {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
}

.navbar-link {
  color: #7F4DE2;
  text-decoration: none;
  font-family: 'IBM Plex Mono', monospace;
  font-weight: 600;
  font-size: 1.1rem;
  padding: 0.25rem 0.5rem;
  border-radius: 6px;
  transition: background 0.18s, color 0.18s, box-shadow 0.18s;
}

.navbar-link:hover, .navbar-link:focus {
  background: #faf5ff;
  color: #FFB43A;
  box-shadow: 0 2px 8px #e9d5ff;
  outline: none;
}

/* Header/hero */
.header-section {
  margin-top: 1.5rem; /* add some space below navbar */
  margin-bottom: 2.7rem;
  padding-top: 3.2rem;
  padding-bottom: 2.3rem;
  background: linear-gradient(90deg, #faf5ff 60%, #f8fafc 100%);
  border: 1.5px solid #ede9fe;
  box-shadow: 0 2px 10px 0 rgba(124, 86, 255, 0.03);
  border-radius: 1.2rem;
  position: relative;
  z-index: 0;
}

.header-title {
  font-size: 2.6rem;
  margin-top: 3.2rem;
  margin-bottom: 1.7rem;
  font-family: 'IBM Plex Mono', monospace;
  font-weight: 700;
  text-align: center;
  letter-spacing: 0.02em;
}

.header-tag {
  font-size: 1.1rem;
  margin-bottom: 1.3rem;
  font-family: 'IBM Plex Mono', monospace;
  font-weight: 400;
  text-align: center;
  letter-spacing: 0.01em;
}

.header-desc {
  font-size: 1.1rem;
  margin-bottom: 2.3rem;
  font-family: 'IBM Plex Mono', monospace;
  font-weight: 400;
  text-align: center;
  letter-spacing: 0.01em;
}

.main-content {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
}

.section-gap {
  margin-bottom: 2.5rem;
}

/* Footer */
.footer {
  width: 100%;
  background: linear-gradient(120deg, #fff 0%, #f3f4f6 100%);
  box-shadow: 0 4px 24px 0 rgba(124, 86, 255, 0.07);
  border: 1.5px solid #e9d5ff;
  border-radius: 1.25rem;
  padding: 1.4rem 0 1.2rem 0;
  margin: 0;
  position: relative;
  left: 0;
  right: 0;
  bottom: 0;
  min-height: 60px;
  box-shadow: 0 -8px 32px 0 rgba(124, 86, 255, 0.13), 0 -1px 0 0 #e9d5ff;
  flex-shrink: 0;
}

.footer-divider {
  height: 28px;
  background: linear-gradient(180deg, rgba(249,245,255,0) 0%, #ede9fe 100%);
  width: 100%;
  margin-bottom: -10px;
}

.footer-inner {
  max-width: 1100px;
  margin: 0 auto;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  gap: 2.5rem;
  width: 100%;
  padding: 0 1.2rem;
  min-height: 60px;
}

.footer-copyright {
  font-size: 1.01rem;
  color: #6b7280;
  font-family: 'IBM Plex Mono', monospace;
  font-weight: 400;
  letter-spacing: 0.01em;
}

.footer-links {
  display: flex;
  flex-direction: row;
  gap: 1.5rem;
  align-items: center;
}
.footer-links a {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background: transparent;
  color: #7F4DE2;
  text-decoration: none;
  transition: background 0.19s, color 0.19s, box-shadow 0.19s;
  outline: none;
}
.footer-links a:focus {
  background: #ede9fe;
  box-shadow: 0 0 0 2px #a78bfa;
}
.footer-links a:hover {
  background: #ede9fe;
  color: #4c1d95;
}
.footer-links svg {
  width: 32px;
  height: 32px;
  display: block;
  color: inherit;
  background: none;
}
.footer-links svg text {
  font-family: 'IBM Plex Mono', monospace;
  fill: #fff;
  dominant-baseline: middle;
}
@media (max-width: 700px) {
  .footer-inner {
    flex-direction: column;
    align-items: center;
    gap: 1.3rem;
    padding: 0 1rem;
    text-align: center;
  }
  .footer-copyright {
    margin-bottom: 0.8rem;
    text-align: center;
  }
}

/* Card Visuals Responsive */
.card img, .trend-card img, .map-card img, .demographic-card img, .section-context img, .demographic-section img,
.card svg, .trend-card svg, .map-card svg, .demographic-card svg, .section-context svg, .demographic-section svg,
.card canvas, .trend-card canvas, .map-card canvas, .demographic-card canvas, .section-context canvas, .demographic-section canvas {
  max-width: 100%;
  height: auto;
  display: block;
  margin: 0 auto;
  box-sizing: border-box;
}
.card-visual, .card-image, .card-svg {
  max-width: 100%;
  height: auto;
  display: block;
  margin: 0 auto;
  padding: 0.5rem 0;
}
.card, .trend-card, .map-card, .demographic-card, .section-context, .demographic-section {
  overflow: visible;
}

footer .max-w-3xl {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  gap: 2rem;
  width: 100%;
  max-width: 1100px;
  margin: 0 auto;
  padding: 0 2.2rem;
}
footer .text-sm {
  font-size: 1rem;
  color: #222;
  font-family: 'IBM Plex Mono', monospace;
}
footer .flex {
  display: flex;
  flex-direction: row;
  gap: 1.5rem;
  align-items: center;
}
footer a {
  color: #222;
  text-decoration: none;
  transition: color 0.18s;
}
footer a:hover, footer a:focus {
  color: #7F4DE2;
}
footer svg {
  vertical-align: middle;
  color: #222;
  background: none;
}
footer svg text {
  font-family: 'IBM Plex Mono', monospace;
  fill: #fff;
  dominant-baseline: middle;
}
@media (max-width: 700px) {
  footer .max-w-3xl {
    flex-direction: column;
    align-items: flex-start;
    gap: 0.7rem;
    padding: 0 1rem;
  }
  footer .flex {
    gap: 1rem;
  }
}

/* Ensure content never scrolls under sticky navbar */
.App-header {
  padding-top: 5.8rem !important;
}

/* Back to Projects button */
.back-btn {
  display: inline-block;
  background: linear-gradient(90deg, #7F4DE2 0%, #6C63FF 100%);
  color: #fff;
  border: none;
  border-radius: 999px;
  padding: 0.7rem 2.2rem;
