  font-size: 1.1rem;
  cursor: pointer;
  margin: 2.5rem auto 0 auto;
  box-shadow: 0 2px 8px rgba(124, 86, 255, 0.13);
  transition: background 0.2s, box-shadow 0.2s;
}
.back-btn:hover, .back-btn:focus {
  background: linear-gradient(90deg, #B993FF 0%, #7F4DE2 100%);
  color: #FFB43A;
  box-shadow: 0 4px 16px rgba(124, 86, 255, 0.18);
  outline: none;
}

/* Accent bar for headings */
.accent-bar {
  display: block;
  width: 42px;
  height: 5px;
  background: linear-gradient(90deg,#7F4DE2 0%,#FFB43A 100%);
  border-radius: 3px;
  margin: 0 auto 1.2rem auto;
}

/* Section title underline */
h1, h2, h3, h4, h5, h6 {
  font-family: 'IBM Plex Mono', monospace;
  color: #22223B;
  font-weight: 700;
  text-align: center;
  margin-top: 2.8rem;
  margin-bottom: 1.5rem;
  letter-spacing: 0.02em;
}

h1 {
  font-size: 2.6rem;
