    background: #fff !important;
    box-shadow: 0 4px 24px 0 rgba(124, 86, 255, 0.07) !important;
    border: 1.5px solid #e9d5ff !important;
    max-width: 900px;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    transition: box-shadow 0.18s, border 0.18s, transform 0.18s;
  }
  .App-header {
    padding: 1.2rem 0.5rem 1.2rem 0.5rem !important;
    margin-left: 0;
    margin-right: 0;
    max-width: 99vw;
  }
}

/* Accessibility focus states */
:focus-visible {
  outline: 2px solid #FFB43A !important;
  outline-offset: 2px;
}

.navbar-link {
  color: #fff;
  text-decoration: none;
  font-size: 1.1rem;
  transition: color 0.2s;
}
.navbar-link:hover {
  color: #b2e0fe;
}

.App {
  text-align: center;
  max-width: 900px;
  margin: 0 auto;
  padding: 2.5rem 1.5rem 4rem 1.5rem;
  min-height: 100vh;
}

/* Header/hero */
.App-header {
  background: #fff;
  color: #22223B;
  padding: 2.5rem 0 1.5rem 0;
  margin-top: 1.5rem; /* add space below navbar */
  margin-bottom: 2.5rem;
  border-radius: 1.5rem;
  box-shadow: 0 4px 24px 0 rgba(124, 86, 255, 0.07);
  position: relative;
  z-index: 0;
}

button, .button {
  background: linear-gradient(90deg, #7F4DE2 0%, #6C63FF 100%);
  color: #fff;
  border: none;
  border-radius: 999px;
  padding: 0.7rem 2rem;
  font-family: 'IBM Plex Mono', monospace;
  font-weight: 700;
  font-size: 1.1rem;
  cursor: pointer;
  margin: 0.5rem 0.5rem 0.5rem 0;
  box-shadow: 0 2px 8px rgba(124, 86, 255, 0.13);
  transition: background 0.2s, box-shadow 0.2s;
}
button:hover, .button:hover {
  background: linear-gradient(90deg, #B993FF 0%, #7F4DE2 100%);
  box-shadow: 0 4px 16px rgba(124, 86, 255, 0.18);
}

@media (max-width: 700px) {
  .section-context, .demographic-section {
    margin: 1.2rem auto !important;
    padding: 1.5rem 1.2rem !important;
    border-radius: 1.2rem !important;
    background: linear-gradient(120deg, #f8fafc 0%, #faf5ff 100%) !important;
    box-shadow: 0 2px 16px rgba(124, 86, 255, 0.08) !important;
    max-width: 900px;
    width: 100%;
    text-align: center !important;
    align-items: center !important;
    font-size: 1.15rem !important;
    color: #3b0764 !important;
    line-height: 1.7 !important;
  }
  h2 {
    font-size: 2rem;
    margin-top: 2rem;
    margin-bottom: 0.7rem;
  }
}

h3 {
  color: #222;
  font-size: 1.3rem;
  margin-bottom: 0.5rem;
  font-weight: 600;
}

.section-intro, .section-context {
  max-width: 1200px;
  margin: 1rem auto 2rem auto;
  color: #f8f9fa;
  background: rgba(0,0,0,0.08);
  border-radius: 8px;
  padding: 1rem 2rem;
  box-shadow: 0 2px 8px rgba(0,0,0,0.04);
}
.section-context {
  background: #eaf4fb;
  color: #0d6efd;
  margin-bottom: 2rem;
}

.trends-container {
  display: flex !important;
  flex-direction: column !important;
  gap: 2.5rem;
  align-items: center;
  justify-content: center;
  margin-bottom: 2.5rem;
  width: 100%;
}

.card, .map-card, .demographic-card {
  background: #fff;
  border-radius: 1.25rem;
  box-shadow: 0 4px 24px 0 rgba(124, 86, 255, 0.10);
  padding: 2rem 1.5rem;
  margin: 1rem;
  flex: 1 1 350px;
  max-width: 500px;
  display: flex;
  flex-direction: column;
  align-items: center;
  border: 1.5px solid #e9d5ff;
}

.trend-card {
  width: 100%;
  max-width: 1100px;
  margin: 0 auto 2.5rem auto;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.maps-container, .demographics-container {
  display: flex;
  flex-direction: column;
  gap: 2.5rem;
  align-items: center;
  justify-content: center;
  margin-bottom: 2.5rem;
  overflow-x: unset;
  padding-bottom: 0;
  width: 100vw;
  max-width: 100vw;
  margin-left: auto;
  margin-right: auto;
}

.card, .map-card, .demographic-card {
  background: #fff;
  border-radius: 16px;
  box-shadow: 0 2px 16px rgba(13,110,253,0.10);
  padding: 1.5rem 0.5rem 2rem 0.5rem;
  margin-bottom: 2.5rem;
  width: 100%;
  max-width: 100vw;
  min-width: 0;
  min-height: 320px;
  transition: box-shadow 0.2s, transform 0.2s;
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  overflow-x: auto;
}

.trends-container, .maps-container, .demographics-container {
  display: flex;
  flex-direction: column;
  gap: 2.5rem;
  align-items: center;
  justify-content: center;
  margin-bottom: 2.5rem;
  width: 100%;
  padding-bottom: 0;
  max-width: 900px;
  margin-left: auto;
  margin-right: auto;
}

@media (max-width: 700px) {
  .card, .map-card, .demographic-card {
    max-width: 99vw;
    min-width: 96vw;
    padding: 1rem 0.5rem 1rem 0.5rem;
    min-height: 200px;
  }
}

svg {
  max-width: 100vw !important;
  height: auto;
  display: block;
}

.card:hover, .map-card:hover, .demographic-card:hover {
  box-shadow: 0 8px 24px rgba(13,110,253,0.20);
  transform: translateY(-2px) scale(1.01);
}

.visualization-context {
  font-size: 0.95rem;
  color: #555;
  margin-top: 1rem;
  background: #eaf4fb;
  border-radius: 8px;
  padding: 0.8rem 1rem;
}

@media (min-width: 1100px) {
  .trends-container, .maps-container, .demographics-container {
    flex-direction: row;
    flex-wrap: wrap;
    gap: 2.5rem;
    align-items: flex-start;
    overflow-x: unset;
  }
  .card, .map-card, .demographic-card {
    max-width: 100vw;
    min-width: 0;
    flex: 1 1 0;
  }
}

@media (max-width: 700px) {
  .card, .map-card, .demographic-card {
    max-width: 99vw;
    min-width: 96vw;
    padding: 1rem 0.5rem 1rem 0.5rem;
  }
}

::-webkit-scrollbar {
  width: 8px;
  background: #cbe6ff;
}
::-webkit-scrollbar-thumb {
  background: #0d6efd;
  border-radius: 6px;
}

/* Brand axis and label styles for D3/SVG charts */
.brand-axis text, .brand-label {
  font-family: 'IBM Plex Mono', 'Inter', monospace, sans-serif;
  font-size: 1.08rem;
  fill: #3b0764;
  font-weight: 500;
  letter-spacing: -0.01em;
}
.brand-tick text {
  font-family: 'IBM Plex Mono', monospace;
  font-size: 1rem;
  fill: #7F4DE2;
}
.brand-axis path, .brand-axis line {
  stroke: #e9d5ff;
  stroke-width: 2px;
}

/* Make SVGs inside cards/maps responsive */
.card svg, .map-card svg, .demographic-card svg {
  width: 100% !important;
  height: auto !important;
  max-width: 100%;
}

@media (max-width: 900px) {
  .App-header {
    padding: 1.2rem 0.5rem 1.2rem 0.5rem;
    margin-left: 0;
    margin-right: 0;
    max-width: 99vw;
  }
}

/* Navbar */
.glassy-navbar {
  width: 100%;
  background: linear-gradient(120deg, #fff 0%, #f3f4f6 100%);
  box-shadow: 0 4px 24px 0 rgba(124, 86, 255, 0.07);
  border: 1.5px solid #e9d5ff;
  border-radius: 1.25rem;
  padding: 1rem 2rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
  position: sticky;
  top: 0;
  z-index: 999;
}
